import React from 'react'
import { Button, Flex, Image, Text } from '@pancakeswap/uikit'
import styled from 'styled-components'
import { useTranslation } from 'contexts/Localization'
import useTheme from 'hooks/useTheme'
import ConnectWalletButton from 'components/ConnectWalletButton'

const StyledFooterArea = styled(Flex)`
  width: 100%;
  height: 120px;
  max-width: 1200px;
  margin: auto;
  margin-top: 30px;
  align-items: center;
  justify-content: space-between;
`

const Footer: React.FC = () => {
  const { t } = useTranslation()
  const { theme } = useTheme()

  return (
    <StyledFooterArea>
      <Flex flexDirection="column">
        <Flex>
          <Text>{t(`$WATER address: `)}</Text>
          <Text color="primary">{t('0xDD123969879DB234AFB234AB234AFB23')}</Text>
        </Flex>
        <Flex>
          <Text>{t(`waterfinance.io version: `)}</Text>
          <Text color="primary">{t('1.01')}</Text>
        </Flex>
      </Flex>
      <Flex width="300px" alignItems="end" justifyContent="end">
        <Image src="/images/twitter.png" alt="Twitter" width={30} height={30} mr="15px"/>
        <Image src="/images/telegram.png" alt="Telegram" width={30} height={30} mr="15px"/>
        <Image src="/images/pancakeswap.png" alt="Pancakeswap" width={30} height={30} mr="15px"/>
        <Image src="/images/language.png" alt="Language" width={35} height={30}/>
      </Flex>
    </StyledFooterArea>
  )
}

export default Footer
