import React from 'react'
import { Button, Flex, Image, Text } from '@pancakeswap/uikit'
import styled from 'styled-components'
import { useTranslation } from 'contexts/Localization'
import useTheme from 'hooks/useTheme'
import ConnectWalletButton from 'components/ConnectWalletButton'

const StyledTopBarArea = styled(Flex)`
  width: 100%;
  height: 110px;
  max-width: 1400px;
  margin: auto;
  align-items: center;
  justify-content: space-between;
`

const TopBar: React.FC = () => {
  const { t } = useTranslation()
  const { theme } = useTheme()

  return (
    <StyledTopBarArea>
      <Flex width="500px" alignItems="center">
        <Image src="/images/logo.svg" alt="Treaury" width={300} height={80} ml="5px"/>
        <Flex>
          <Flex width="100px">
            <Text bold>{t('Support')}</Text>
            <Image src="/images/support.png" alt="Support" width={20} height={20} ml="5px" />
          </Flex>
          <Flex width="80px">
            <Text bold>{t('Docs')}</Text>
            <Image src="/images/support.png" alt="Support" width={20} height={20} ml="5px" />
          </Flex>
        </Flex>
      </Flex>
      <Flex>
        <Button mr="15px">{t('Buy $WATER')}</Button>
        <ConnectWalletButton />
      </Flex>
    </StyledTopBarArea>
  )
}

export default TopBar
